<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <div>
    <v-container>
      <!-- These padding tweaks are very annoying -->
      <v-list style="padding: 4px 0 0 0" dense nav>
        <v-list-item v-for="button in categories"
                     style="display: inline-block;width: 220px;padding: 10px;text-align: center;"
                     link :to="'/l/' + button">
          <span>{{ effectiveEnglishToggle && englishCategoryNames[button] || button }}</span>
        </v-list-item>

        <v-progress-circular indeterminate style="width: 220px" v-if="!!restCallsGoing" />

        <span v-if="showLanguageToggle" style="float:right; margin: -10px 0; padding-left: 20px">
          <span class="flagButton" @click="languageCfg.englishToggle = false">🇷🇴</span>
          <v-switch class="floatingToggle" v-model="languageCfg.englishToggle" />
          <span class="flagButton" @click="languageCfg.englishToggle = true">🇬🇧</span>
        </span>
        <span style="float:right; margin: -10px 0;">
          <v-icon @click="narrow = false">mdi-arrow-expand-horizontal</v-icon>
          <v-switch class="floatingToggle" v-model="narrow" />
          <v-icon @click="narrow = true">mdi-arrow-collapse-horizontal</v-icon>
        </span>

      </v-list>
    </v-container>
    <v-container fluid :style="{'max-width': narrow && '675px' || null}">

<!-- Commented out because errors from missing backend are expected when the backend is missing, though in general a more flexible approach would be good -->
<!--      <Notifications :alerts="alerts" />-->

      <LiteratureCollection v-if="!!rootCollection"
                            :category="category" :collection="rootCollection" />
    </v-container>
  </div>
</template>

<script>
  import axios from 'axios';
  import router from '../plugins/router.js';
  import AxiosHelper from '../components/AxiosHelper.vue';
  import LiteratureCollection from '../components/LiteratureCollection.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: {LiteratureCollection, Notifications},
    mixins: [AxiosHelper],

    props: ['category', 'backendPresent', 'loggedIn'],

    data: () => ({
      staticRootCollections: {
        // Do not put subCollections here unless you're mad
        'Eseuri': {
          entries: [{
            title: 'This Should Never Happen',
            mdPath: '/essay/ThisShouldNeverHappen.md',
            lang: 'en'
          },{
            title: 'Întrebări fără răspuns despre organizarea societății și mersul lumii',
            mdPath: '/essay/IntrebariFaraRaspuns.md',
            lang: 'ro'
          }]
        }
      },

      englishCategoryNames: {
        'Eseuri': 'Essays',
        'Poezii': 'Poetry'
      },

      languageCfg: {
        romanianExists: false,
        englishExists: false,
        englishToggle: false
      },
      narrow: true,

      loadedCategories: [],
      loadedRootCollection: null
    }),

    computed: {
      showLanguageToggle () {
        return this.romanianExists && this.englishExists;
      },

      effectiveEnglishToggle () {
        // TODO This logic
        return true;
      },

      categories () {
        let set = new Set(this.loadedCategories || []);
        Object.keys(this.staticRootCollections).forEach(key => set.add(key));
        return set;
      },

      rootCollection () {
        let absrc = this.absolutiseLoadedEntries(this.loadedRootCollection)
        console.log('Literature :: rootCollection :: absrc =', absrc);

        let staticRootEntries = (this.staticRootCollections && this.staticRootCollections[this.category] && this.staticRootCollections[this.category].entries || [])
          .map(e => Object.assign({}, e, { mdPath: this.$localDataUrl + e.mdPath }));
        let entries = (absrc && absrc.entries || []).concat(staticRootEntries);
        console.log('Literature :: rootCollection :: entries =', entries);

        return {
          title: this.category,
          entries,
          // Not mapping static subCollecitons in the present circumstance because we'd have to recursively perform the above path hacks
          subCollections: absrc && absrc.subCollections || []
        };
      }
    },

    methods: {
      loadToc() {
        this.axiosHelper('loading TOC', () => axios.get(this.$apiUrl + '/l/toc/' + encodeURIComponent(this.category)),
          data => this.loadedRootCollection = data);
      },

      absolutiseLoadedEntries (collection) {
        if (!(collection && collection.entries))
          return collection;

        let entries = collection.entries.map(e =>
          Object.assign({}, e, {
            htmlPath: this.absolutisePath(e.htmlPath),
            mdPath: this.absolutisePath(e.mdPath)
          }));
        let subCollections = (collection.subCollections || []).map(this.absolutiseLoadedEntries);
        return Object.assign({}, collection, {entries, subCollections});
      },

      absolutisePath(path) {
        // TODO This base URL manoeuvres are fragile and strange
        return path && this.$apiUrl + '/l/toc/' + encodeURIComponent(this.category) + '/' + encodeURIComponent(path)
      },

      navigateToFirstCategoryIfNeeded () {
        if (!this.category && !!this.categories.size) {
          // This preposterous set idiom again...
          router.replace('/l/' + this.categories.entries().next().value[0]);
        }
      }

      /* TODO modify URL based on what's open and use it too (now with backend-loaded data, this will require an approach
         similar to the filters of Gagmine). Scavenged code for that:
      watch: {
        active(v) {
          let path = '/essay/' + (v>0 && this.entries[v-1].filename || '');
          if (this.$route.path !== path)
            router.replace(path);
        }
      },

      expandEntryFromUrl() {
        for (let i=0; i<this.entries.length; i++) {
          if (this.entries[i].filename === this.filename) {
            this.active = i+1;
            return;
          }
        }
        this.active = 0;
      }
      */
    },

    watch: {
      category() {
        this.loadToc();
      }
    },

    beforeMount () {
      if (!this.categories.length)
        this.axiosHelper('loading categories', () => axios.get(this.$apiUrl + '/l/categories'),
          data => this.loadedCategories = data.d)
          .then(this.navigateToFirstCategoryIfNeeded);
    },

    mounted () {
      this.loadToc();
    }
  }
</script>

<style>
  .outer-panel > .v-expansion-panel-content__wrap {
    padding-left: 0;
    padding-right: 0;
  }
  .flagButton {
    display: inline-block;
    width: 24px;
    height: 24px;
    cursor: pointer;
    vertical-align: middle;
  }
  /* Copied what Vue gave for min-height and added opacity * /
  TODO Reenable these (perhaps with better classes) when we can regulate the presence of titles _in_ the content
  .v-expansion-panel-header {
    transition: opacity,min-height .3s cubic-bezier(.25,.8,.5,1);
  }
  / * Make text in the accordion handle invisible when the entry is showing, because it begins with that very same title * /
  .v-expansion-panel-header--active {
    opacity: 0;
  }*/
</style>
